@import url(https://fonts.googleapis.com/css?family=Montserrat);
@import url(https://fonts.googleapis.com/css?family=Montserrat);
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.App {
  text-align: center;
}

body {
  background-color: #FFCC00;
  padding: 20px;
  margin: 0;
  font-family: 'Montserrat', sans-serif;
}

.header {
  background-color: #111;
  padding: 0;
  display: flex;;
}

ul.header li {
  list-style-type: none;
}

ul.header li a {
  color: gray;
  text-decoration: none;
  padding: 20px;
  display: inline-block;
}

ul.header li a:hover {
  text-decoration: underline;
}

ul.header button {
  position: absolute;
  right: 5%;
  margin: 10px;
}

.content {
  padding: 20px;
}
.form-container {
  padding: 5rem 0rem 5rem 0rem
}

.completeBtn {
  height: 50px;
  width: 250px;
  padding: 10px 15px;
  font-size: 20px;
  font-family: "Montserrat", sans-serif;
  font-weight: bold;
}

.note {
  margin: 15rem 0rem 2rem 0rem;
}

/* for mobile screens */
@media only screen and (max-width: 600px) {
 .completeBtn {
  width: 10rem;
 }
 .note {
  margin: 0 auto;
 }
}
.challenge {
  font-size: 45px;
}
.prompt {
  margin: 2rem 0;
}
  .analytics-card {
    width: 100%;
    height: 100%;
    min-height: 400px;
    min-width: 0;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    background-color: white;
    position: relative;
    margin: 10px 0px;
    word-wrap: break-word;

  }
  
  #chart_div {
    width: 100%;
    height: 100%;
  }
  .col-md-6 {
    padding-right: 10px;
  }
  .card-contents{
    margin-top: 10%;
  }

  #total-card {
    width: 100%;
  }

  .total-completed-challenges {
    font-size: 250%;
    font-weight: bold;
  }

  .card-title {
    font-family: 'Montserrat', sans-serif;
  }
  
  .challenge-history {
    text-align: left;
    font-size: 80%;
  }

 .register-input {
   width: 75%;
   margin: auto;
   margin-bottom: 10px;
 }
 
 .form-validation {
  color: red;
  display: block;
 }
 
 .card {
   width: 500px;
   display: flex;
   flex-direction: row;
   justify-content: space-around;
 }
 

/* for mobile screens */
@media only screen and (max-width: 600px) {
 .card {
  width: 400px;
 }
 .register-input {
  width: 85%;
 }
}

.card-contents{
   margin: 5%;
 }

 .register-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
 }
